import React, { Component } from 'react';
import { Row, Col, Button } from 'reactstrap';

import USMap from './components/USMap';
import WorldMap from './components/WorldMap';
import PhotoViewer from './components/PhotoViewer';
import { StatesArray, CountriesArray } from '../../maps/VisitedPlaces';

class ScrapbookPage extends Component {
  constructor(props) {
    super(props);

    this.state = {
      chosenGraph: 'us',
      chosenArea: 'California',
    };

    this.changeGraph = this.changeGraph.bind(this);
    this.clickGraph = this.clickGraph.bind(this);
  }

  changeGraph(name) {
    this.setState({
      chosenGraph: name,
    });
  }

  clickGraph(e) {
    if (StatesArray.includes(e.point['hc-key'] || CountriesArray.includes(e.point['hc-key']))) {
      this.setState({
        chosenArea: e.point.name
      });
    }
  }

  render() {
    const { chosenGraph, chosenArea } = this.state;
    console.log(this.state)
    return (
      <div>
        <Row>
          <Col xs="3"></Col>
          <Col xs="3">
            <Button
              block
              color="primary"
              outline
              active={(chosenGraph === 'us') ? true : false}
              onClick={() => this.changeGraph('us')}
            >
              United States
            </Button>
          </Col>
          <Col xs="3">
            <Button
              block
              color="primary"
              outline
              active={(chosenGraph === 'world') ? true : false}
              onClick={() => this.changeGraph('world')}
            >
              World
            </Button>
          </Col>
          <Col xs="3"></Col>
        </Row>
        <Row>
          <Col xs="12">
            {(chosenGraph === 'us') ? 
              <USMap
                clickGraph={this.clickGraph}
              />
              :
              <WorldMap
                clickGraph={this.clickGraph}
              />
            }
          </Col>
        </Row>
        <PhotoViewer
          chosenArea={chosenArea}
        />
      </div>
    )
  }
}

export default ScrapbookPage;