import React from 'react';
import Highcharts from 'highcharts';
import HighchartsReact from 'highcharts-react-official';

import WorldMapData from '../../../maps/World';
import { CountriesArray, CountriesArrayNoPic } from '../../../maps/VisitedPlaces';

require('highcharts/modules/map')(Highcharts);

const WorldMap = (props) => {
  const {
    clickGraph,
  } = props;

  const mapOptions = {
    chart: {
      map: WorldMapData,
    },
    title: {
      text: ''
    },
    subtitle: {
      text: '* Click a country to see some pictures!',
      align: 'right',
    },
    legend: {
      enabled: true,
    },
    plotOptions: {
      map: {
        allAreas: false,
      },
      series:{
        point:{
          events:{
            click: (e) => clickGraph(e)
          }
        }
      }
    },
    credits: {
      enabled: false,
    },
    series: [
      {
        allAreas: true,
        showInLegend: false,
      },
      {
        name: 'Countries Visted With Pictures',
        data: CountriesArray.map(a => {
          return([a, 1])
        }),
        color: '#7cb5ec',
        cursor: 'pointer',
      },
      {
        name: 'Countries Visted, No Pictures',
        data: CountriesArrayNoPic.map(a => {
          return([a, 1])
        }),
        color: '#90ed7d'
      }
    ]
  }
  return (
    <div>
      <HighchartsReact
        options={mapOptions}
        constructorType={'mapChart'}
        highcharts={Highcharts}
      />
    </div>
  )
}

export default WorldMap;