import React from 'react';
import Highcharts from 'highcharts';
import HighchartsReact from 'highcharts-react-official';

import USMapData from '../../../maps/UnitedStates';
import { StatesArray, StatesArrayNoPic } from '../../../maps/VisitedPlaces';

require('highcharts/modules/map')(Highcharts);

const USMap = (props) => {
  const {
    clickGraph
  } = props;
  const mapOptions = {
    chart: {
      map: USMapData,
    },
    title: {
      text: ''
    },
    subtitle: {
      text: '* Click a state to see some pictures!',
      align: 'right',
    },
    legend: {
      enabled: true,
    },
    plotOptions: {
      map: {
        allAreas: false,
      },
      series:{
        point:{
          events:{
            click: (e) => clickGraph(e)
          }
        }
      }
    },
    credits: {
      enabled: false,
    },
    series: [
      {
        allAreas: true,
        showInLegend: false,
      },
      {
        name: 'States Visted With Pictures',
        data: StatesArray.map(state => {
          return([state, 1])
        }),
        color: '#7cb5ec',
        cursor: 'pointer',
      },
      {
        name: 'States Visted, No Pictures',
        data: StatesArrayNoPic.map(state => {
          return([state, 1])
        }),
        color: '#90ed7d'
      }
    ]
  };
  return (
    <div>
      <HighchartsReact
        options={mapOptions}
        constructorType={'mapChart'}
        highcharts={Highcharts}
      />
    </div>
  )
}

export default USMap;