import React from 'react';
import { Link, withRouter } from 'react-router-dom';
import { Row, Col } from 'reactstrap';

const TopRouter = (props) => {
  const { history } = props;

  return (
    <div>
      <Row style={{height: '10vh', paddingTop: '2vh'}}>
        <Col xs="2"></Col>
        <Col xs="2">
          <Link to="/Home" style={{textDecoration: 'none', color: 'inherit'}}>
            <h4 
              className="header-link" 
              style={(history.location.pathname.includes('Home')) ? {textDecoration: 'underline'} : {}}
            >
              Home
            </h4>
          </Link>
        </Col>
        <Col xs="2">
          <Link to="/Career" style={{textDecoration: 'none', color: 'inherit'}}>
            <h4 
              className="header-link"
              style={(history.location.pathname.includes('Career')) ? {textDecoration: 'underline'} : {}}
            >
              Career
            </h4>
          </Link>
        </Col>
        <Col xs="2">
          <Link to="/Projects" style={{textDecoration: 'none', color: 'inherit'}}>
            <h4 
              className="header-link"
              style={(history.location.pathname.includes('Projects')) ? {textDecoration: 'underline'} : {}}
            >
              Projects
            </h4>
          </Link>
        </Col>
        <Col xs="2">
          <Link to="/Scrapbook" style={{textDecoration: 'none', color: 'inherit'}}>
            <h4 
              className="header-link"
              style={(history.location.pathname.includes('Scrapbook')) ? {textDecoration: 'underline'} : {}}
            >
              Scrapbook
            </h4>
          </Link>
        </Col>
        <Col xs="2"></Col>
      </Row>
    </div>
  )
}

export default withRouter(TopRouter);