import React from 'react';
import { Row, Col, Badge } from 'reactstrap';

import MarketDashboardPicture from '../../assets/img/Projects/MarketDashboard.png';
import PredictorPicture from '../../assets/img/Projects/Predictor.JPG';
import GithubIcon from '../../assets/img/Icons/GithubIcon.png';
const ProjectsPage = () => {
  return (
    <div>
      <Row>
        <Col xs="5">
          <img src={MarketDashboardPicture} style={{width: '100%', height: '40vh'}} alt="Swing"></img>
        </Col>
        <Col xs="7" style={{marginTop: '15px', position: 'relative'}}>
          <h4>Ryan's Market Dashboard</h4>
          <ul style={{paddingLeft: '20px', marginTop: '7px', marginBottom: '7px'}}>
            <li>
              Experimental project to play with Material UI.
            </li>
            <li>
              Backend hits endpoints to <a href="https://www.alphavantage.co/">AlphaVantage</a> and the <a href="https://www.bea.gov/">Bureau of Economic Analysis</a>.
            </li>
            <li>
              Backend also web scrapes <a href="https://finance.yahoo.com/">Yahoo Finance</a>
            </li>
          </ul>
          <Badge style={{backgroundColor: '#f1d63b', color: '#000000', margin: '0 0.5em'}}>Javascript</Badge>
          <Badge style={{backgroundColor: '#61dafb', color: '#ffffff', margin: '0 0.5em'}}>React</Badge>
          <Badge style={{backgroundColor: '#f1d63b', color: '#000000', margin: '0 0.5em'}}>Express</Badge>
          <Row style={{position: 'absolute', bottom: 0}}>
            <Col xs="12">
              <img className="skill-icon" src={GithubIcon} style={{height: '60px', width: '60px'}} alt="Github Icon"></img>
            </Col>
          </Row>
        </Col>
      </Row>
      <hr color="#cfcfcf"></hr>
      <Row>
        <Col xs="5">
          <img src={PredictorPicture} style={{width: '100%', height: '40vh'}} alt="Predictor"></img>
        </Col>
        <Col xs="7" style={{marginTop: '15px', position: 'relative'}}>
          <h4>Stock Buying Algorithm</h4>
          <ul style={{paddingLeft: '20px', marginTop: '7px', marginBottom: '7px'}}>
            <li>
              The classic get rich quick dream.
            </li>
            <li>
              Hits endpoints to <a href="https://www.alphavantage.co/">AlphaVantage</a>.
            </li>
            <li>
              ~75% chance to go positive, with an average gain of 3-4% within a month or two of purchase. However, the time frames in which tests were run were relatively bullish.
            </li>
          </ul>
          <Badge style={{backgroundColor: '#357be9', color: '#ffffff', margin: '0 0.5em'}}>Python</Badge>
          <Row style={{position: 'absolute', bottom: 0}}>
            <Col xs="12">
              <img className="skill-icon" src={GithubIcon} style={{height: '60px', width: '60px'}} alt="Github Icon"></img>
              <small>*Disclaimer: I don't really use this to buy stocks, so you shouldn't either!</small>
            </Col>
          </Row>
        </Col>
      </Row>
    </div>
  )
}

export default ProjectsPage;