import React from 'react';
import { Row, Col, Badge } from 'reactstrap';

import { VerticalTimeline, VerticalTimelineElement }  from 'react-vertical-timeline-component';
import 'react-vertical-timeline-component/style.min.css';
import WorkIcon from '@material-ui/icons/Work';
import SchoolIcon from '@material-ui/icons/School';
import StarIcon from '@material-ui/icons/Star';

import NorthropIcon from '../../assets/img/Icons/NorthropGrummanLogo.png';
import USCIcon from '../../assets/img/Icons/USCIcon.jpg';
import UsamriidIcon from '../../assets/img/Icons/UsamriidLogo.png';

const CareerPage = () => {
  return (
    <div>
      <Row style={{height: '100%', backgroundColor: '#e3e3e3'}}>
        <Col xs="12">
          <VerticalTimeline layout={'1-column'}>
            <VerticalTimelineElement
              contentStyle={{borderTop: '3px solid #349ff4'}}
              date="2018 - Present"
              iconStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}
              icon={<WorkIcon />}
            >
              <Row>
                <Col xs="9">
                  <h3 className="vertical-timeline-element-title">Northrop Grumman</h3>
                  <h4 className="vertical-timeline-element-subtitle">Full Stack Web Developer</h4>
                  <ul style={{paddingLeft: '20px', marginTop: '7px', marginBottom: '0px'}}>
                    <li>
                      Designed and built a fully integrated change request system
                    </li>
                    <li>
                      Helped develop the backend of an image classification tool
                    </li>
                  </ul>
                  <Badge style={{backgroundColor: '#f1d63b', color: '#000000', margin: '0 0.5em'}}>Javascript</Badge>
                  <Badge style={{backgroundColor: '#61dafb', color: '#ffffff', margin: '0 0.5em'}}>React</Badge>
                  <Badge style={{backgroundColor: '#41b883', color: '#ffffff', margin: '0 0.5em'}}>Vue</Badge>
                  <Badge style={{backgroundColor: '#f1d63b', color: '#000000', margin: '0 0.5em'}}>Express</Badge>
                </Col>
                <Col xs="3" className="text-center">
                  <img src={NorthropIcon} style={{width: '115px', height: '115px'}} alt="Northrop"></img>
                </Col>
              </Row>
            </VerticalTimelineElement>
            <VerticalTimelineElement
              contentStyle={{ borderTop: '3px solid #349ff4' }}
              date="2017 - 2018"
              iconStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}
              icon={<WorkIcon />}
            >
              <Row>
                <Col xs="9">
                  <h3 className="vertical-timeline-element-title">Northrop Grumman</h3>
                  <h4 className="vertical-timeline-element-subtitle">Electrical Engineer</h4>
                  <ul style={{paddingLeft: '20px', marginTop: '7px', marginBottom: '0px'}}>
                    <li>
                      Developed a beta web app to keep track of satellite command changes
                    </li>
                    <li>
                      Helped revise over 250 electrical design diagrams
                    </li>
                  </ul>
                  <Badge style={{backgroundColor: '#f1d63b', color: '#000000', margin: '0 0.5em'}}>Javascript</Badge>
                  <Badge style={{backgroundColor: '#858fb9', color: '#ffffff', margin: '0 0.5em'}}>PHP</Badge>
                  <Badge style={{backgroundColor: '#e86229', color: '#ffffff', margin: '0 0.5em'}}>HTML</Badge>
                </Col>
                <Col xs="3" className="text-center">
                  <img src={NorthropIcon} style={{width: '115px', height: '115px'}} alt="Northrop 2"></img>
                </Col>
              </Row>
            </VerticalTimelineElement>
            <VerticalTimelineElement
              contentStyle={{ borderTop: '3px solid #e91e63' }}
              date="2013 - 2017"
              iconStyle={{ background: 'rgb(233, 30, 99)', color: '#fff' }}
              icon={<SchoolIcon />}
            >
              <Row>
                <Col xs="9">
                  <h3 className="vertical-timeline-element-title">Graduation</h3>
                  <h4 className="vertical-timeline-element-subtitle">B.S. Electrial Engineering, Computer Engineering</h4>
                </Col>
                <Col xs="3" className="text-center">
                  <img src={USCIcon} style={{width: '115px', height: '115px'}} alt="USC"></img>
                </Col>
              </Row>
            </VerticalTimelineElement>
            <VerticalTimelineElement
              contentStyle={{borderTop: '3px solid #349ff4'}}
              date="June 2016 - August 2016"
              iconStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}
              icon={<WorkIcon />}
            >
              <Row>
                <Col xs="9">
                  <h3 className="vertical-timeline-element-title">USAMRIID</h3>
                  <h4 className="vertical-timeline-element-subtitle">Software Development Intern</h4>
                  <ul style={{paddingLeft: '20px', marginTop: '7px', marginBottom: '0px'}}>
                    <li>
                      Designed and built custom python scripts for data manipulation and analysis
                    </li>
                    <li>
                      Fixed a critical flaw in a web app for experiment management
                    </li>
                  </ul>
                  <Badge style={{backgroundColor: '#357be9', color: '#ffffff', margin: '0 0.5em'}}>Python</Badge>
                  <Badge style={{backgroundColor: '#f1d63b', color: '#000000', margin: '0 0.5em'}}>Javascript</Badge>
                  <Badge style={{backgroundColor: '#858fb9', color: '#ffffff', margin: '0 0.5em'}}>PHP</Badge>
                  <Badge style={{backgroundColor: '#e86229', color: '#ffffff', margin: '0 0.5em'}}>HTML</Badge>
                </Col>
                <Col xs="3" className="text-center">
                  <img src={UsamriidIcon} style={{width: '115px', height: '115px'}} alt="USAMRIID"></img>
                </Col>
              </Row>
            </VerticalTimelineElement>
            <VerticalTimelineElement
              contentStyle={{borderTop: '3px solid #349ff4'}}
              date="June 2015 - August 2015"
              iconStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}
              icon={<WorkIcon />}
            >
              <Row>
                <Col xs="9">
                  <h3 className="vertical-timeline-element-title">USC Image Processing and Informatics Lab</h3>
                  <h4 className="vertical-timeline-element-subtitle">Software Development Intern</h4>
                  <ul style={{paddingLeft: '20px', marginTop: '7px', marginBottom: '0px'}}>
                    <li>
                      Developed image processing software for tumor recognition in CT scans
                    </li>
                  </ul>
                  <Badge style={{backgroundColor: '#aaaaaa', color: '#ffffff', margin: '0 0.5em'}}>Microsoft Visual Basic</Badge>
                </Col>
                <Col xs="3" className="text-center">
                  <img src={USCIcon} style={{width: '115px', height: '115px'}} alt="USC 2"></img>
                </Col>
              </Row>
            </VerticalTimelineElement>
            <VerticalTimelineElement
              iconStyle={{ background: 'rgb(16, 204, 82)', color: '#fff' }}
              icon={<StarIcon />}
            />
          </VerticalTimeline>
        </Col>
      </Row>
    </div>
  )
}

export default CareerPage;