import React from 'react';
import { Row } from 'reactstrap';
import LazyLoad from 'react-lazyload';

import Photos from '../../../assets/img/Scrapbook';

const PhotoViewer = (props) => {
  const { 
    chosenArea,
  } = props;
  return (
    <div>
      <h2>{chosenArea}</h2>
      {Photos.hasOwnProperty(chosenArea) && Object.keys(Photos[chosenArea]).map((location) => (
        <div key={location}>
          <h3>{location}</h3>
          <Row>
            {Photos[chosenArea][location].map((pic, index) => (
              <LazyLoad height={175} key={index}>
                <img src={pic} style={{height: '175px', width: '175px', margin: '0px 15px 0px 15px'}} alt={location}></img>
              </LazyLoad>
            ))}
          </Row>
          <hr></hr>
        </div>
      ))}
    </div>
  )  
}

export default PhotoViewer;